<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`ImportLcIssued${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import ImportLcIssuedListLayout from '@/views/trade/import-lc/import-lc-issued/import-lc-issued-browse/ImportLcIssuedListLayout'
import ImportLcIssuedCardLayout from '@/views/trade/import-lc/import-lc-issued/import-lc-issued-browse/ImportLcIssuedCardLayout'
import ImportLcIssuedPivotLayout from '@/views/trade/import-lc/import-lc-issued/import-lc-issued-browse/ImportLcIssuedPivotLayout'
import ImportLcIssuedTableMixin from '@/mixins/trade/import-lc/ImportLcIssuedTableMixin'

export default {
  name: 'import-lc-issued-browse',
  mixins: [ImportLcIssuedTableMixin],
  components: {
    ImportLcIssuedListLayout,
    ImportLcIssuedCardLayout,
    ImportLcIssuedPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    let date = new Date()
    this.fromDate = date.setDate(date.getDate() - 30)
    this.loadImportLcTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
