import TradeFinanceService from '@/services/trade/TradeFinanceService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'

let ImportLcIssuedTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin, DatePickerMixin],
  data: function () {
    return {
      importLcTableData: [],
      importLcTableContainer: 'loading-container',
      importLcTableBusy: false,
      importLcTableTotalRows: 0,
      importLcTableCurrentPage: 1,
      importLcTablePerPage: 10,
      fromDate: null,
      toDate: new Date(),
      columnDefs: [{
        headerName: this.$t('LC_NUMBER'),
        field: 'lcNumber',
        format: 'Number'
      }, {
        headerName: this.$t('LC_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('LC_BENEFICIARY'),
        field: 'beneficiaryName'
      }, {
        headerName: this.$t('ISSUANCE_DATE'),
        field: 'issuanceDate',
        format: 'Date'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('AMOUNT'),
        field: 'lcAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    importLcTableNoDataText: function () {
      return this.importLcTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    importLcTableTotalPages: function () {
      return Math.ceil(this.importLcTableTotalRows / this.importLcTablePerPage)
    },
    importLcTablePagingInfo: function () {
      return this.pagingInfo(this.importLcTableCurrentPage, this.importLcTablePerPage, this.importLcTableTotalRows)
    }
  },
  methods: {
    loadImportLcTableData (loadingContainerId) {
      let self = this
      this.importLcTableBusy = true
      this.importLcTableContainer = loadingContainerId || this.importLcTableContainer
      this.startLoading(this.importLcTableContainer)
      TradeFinanceService.issuedImportLcs({
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.stopLoading(self.importLcTableContainer)
        self.importLcTableBusy = false
        self.importLcTablePerPage = response.data.totalRows
        self.importLcTableTotalRows = response.data.totalRows
        self.importLcTableData = response.data.lcList || []
      }).catch(function (error) {
        self.stopLoading(self.importLcTableContainer)
        self.importLcTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshImportLcTable () {
      this.loadImportLcTableData()
    }
  },
  watch: {
    importLcTableCurrentPage: function () {
      this.loadImportLcTableData()
    }
  }
}

export default ImportLcIssuedTableMixin
